import store from "@/store";
function hackWarn() {
  const browser = browserName();
  const colorSupportedBrowsers = ["Chrome", "Firefox"];
  const hackMessage = "ഹാക്ക് ചെയ്തതാ കൊല്ലും പന്നി 😀 !!!";
  if (colorSupportedBrowsers.indexOf(browser) >= 0) {
    console.log(
      "%c " + hackMessage,
      "padding:5px;background: #fdea18; color: #f206ab;border-radius:10px"
    );
  } else {
    console.log(hackMessage);
  }
}

function calculateSize() {
  const minHeightToMap = 500;
  let subtractHeight = 50;
  let availableVerticalSpace = (window.innerHeight - 335);
  let heightToMap = availableVerticalSpace;
  if (window.innerHeight > window.innerWidth) {
    heightToMap = window.innerWidth;
    subtractHeight = 100;
  }
  if (heightToMap <= minHeightToMap) heightToMap = minHeightToMap;
  heightToMap -= subtractHeight;
  store.state.boardWidth = heightToMap;
  store.state.boardHeight = heightToMap;
}

function browserName() {
  if (
    (!!window.opr && !!opr.addons) ||
    !!window.opera ||
    navigator.userAgent.indexOf(" OPR/") >= 0
  ) {
    return "Opera";
  } else if (
    !!window.chrome &&
    (!!window.chrome.webstore || !!window.chrome.runtime)
  ) {
    return "Chrome";
  } else if (
    /constructor/i.test(window.HTMLElement) ||
    (function (p) {
      return p.toString() === "[object SafariRemoteNotification]";
    })(
      !window["safari"] ||
      (typeof safari !== "undefined" && safari.pushNotification)
    )
  ) {
    return "Safari";
  } else if (typeof InstallTrigger !== "undefined") {
    return "Firefox";
  } else if (/*@cc_on!@*/ false || !!document.documentMode) {
    return "IE";
  } else if (!!window.StyleMedia) {
    return "Edge";
  } else {
    return "unknown";
  }
}

export default {
  hackWarn,
  browserName,
  calculateSize
};
