import ColyseusClient from "@/colyseusClient";
import store from "@/store";
import helper from "@/helpers";
import router from "@/router";

function initMainRoom() {
    store.state.browser = helper.browser.browserName();
    updateRoomList();
    ColyseusClient.getAvailableRooms("mainRoom")
        .then(rooms => {
            if (!rooms.length) {
                ColyseusClient.joinOrCreate("mainRoom")
                    .then(room => {
                        addMainRoomOnMessage(room);
                    })
                    .catch(e => {
                        console.error("join error", e);
                    });
            } else {
                ColyseusClient.joinById(rooms[0].roomId)
                    .then(room => {
                        addMainRoomOnMessage(room);
                    })
                    .catch(e => {
                        console.error("join error", e);
                    });
            }
        })
        .catch(e => {
            console.error(e);
        });
}

function checkReJoinAvailable() {
    let room = helper.localStorage.get("room");
    if (room) {
        reJoin(room.roomId, room.sessionId);
    }
    else {
        loader(false);
    }
}

function createBingoRoom(roomAlias) {
    loader(true);
    const { player } = store.state;
    store.state.room.myRoomAlias = roomAlias;
    ColyseusClient.create("bingoRoom", { roomAlias, player })
        .then(room => {
            loader(false);
            addStateChangeLitsener(room);
            router.replace({
                name: 'joinedRoom', params: {
                    roomId: room.id,
                    playerId: room.sessionId
                }
            });
        })
        .catch(e => {
            loader(false);
            console.error("join error", e);
        });
}

function joinBingoRoom(roomId, updateMainRoom) {
    loader(true);
    const { player } = store.state;
    ColyseusClient.joinById(roomId, { player })
        .then(room => {
            loader(false);
            addStateChangeLitsener(room, updateMainRoom);
            router.replace({
                name: 'joinedRoom', params: {
                    roomId: room.id,
                    playerId: room.sessionId
                }
            });
        })
        .catch(e => {
            loader(false);
            router.replace('/');
            updateRoomList();
            // console.log("join error", e);
        });
}

function reJoin(roomId, sessionId, updateMainRoom = true) {
    loader(true);
    ColyseusClient.reconnect(roomId, sessionId).then(room => {
        loader(false);
        addStateChangeLitsener(room, updateMainRoom);
    }).catch(e => {
        joinBingoRoom(roomId, updateMainRoom);
    });
}

function addMainRoomOnMessage(room) {
    // checkReJoinAvailable();
    store.state.room.mainRoomObject = room;
    room.onMessage(message => {
        handleMainRoomIncomingMessage(message);
    });
}

function handleMainRoomIncomingMessage(message) {
    if (message === "ROOM_UPDATE") {
        updateRoomList();
    }
}

function handleBingoRoomIncomingMessage(message) {
    switch (message.type) {
        case "POP_EMOJI":
            showPopedEmoji(message);
            break;
        default:
            break;
    }
}

function showPopedEmoji({ emojiName, playerId }) {
    store.commit('popEmoji', { emojiName, playerId });
}

function updateRoomList() {
    ColyseusClient.getAvailableRooms("bingoRoom").then(rooms => {
        store.state.room.roomsList = rooms;
    });
}

function addStateChangeLitsener(room, updateMainRoom = true) {
    store.state.room.myRoomId = room.id;
    store.state.room.bingoRoomObject = room;
    if (updateMainRoom) {
        initiateRoomUpdate();
    }
    room.onStateChange(state => {
        store.state.room.myRoom = state.players.toJSON();
        store.state.player.id = room.sessionId;
        store.state.room.myAdminPlayer = state.currentPlayer;
        store.state.room.myRoomCurrentPlayer = state.currentPlayer;
        store.state.room.gameStarted = state.gameStarted;
        store.state.room.myRoomAdmin = state.adminPlayer;
        store.state.room.myRoomAdmin = state.adminPlayer;
        store.state.room.playerTimeout = state.playerTimeout;
        store.state.room.offlinePlayerTimeout = state.offlinePlayerTimeout;
        store.state.room.elapsedTime = state.elapsedTime;
    });
    room.onLeave((code) => {
        loader(false);
        removeRoom();
    });
    room.onMessage(message => {
        handleBingoRoomIncomingMessage(message);
    });

}

function playMyTurn(number) {
    const { bingoRoomObject } = store.state.room;
    bingoRoomObject.send({ type: "PLAY_MY_TURN", number });
}

function popEmoji(emojiName) {
    const { bingoRoomObject } = store.state.room;
    showPopedEmoji({ playerId: store.state.player.id, emojiName });
    bingoRoomObject.send({ type: "POP_EMOJI", emojiName });
}

function initiateRoomUpdate() {
    const { mainRoomObject } = store.state.room;
    mainRoomObject.send("ROOM_UPDATE");
}

function startGame() {
    const { bingoRoomObject } = store.state.room;
    bingoRoomObject.send({ type: "START_GAME" });
}

function reopenRoom() {
    const { bingoRoomObject } = store.state.room;
    bingoRoomObject.send({ type: "RE_OPEN_ROOM" });
    initiateRoomUpdate();
}

function leaveRoom() {
    const { bingoRoomObject } = store.state.room;
    bingoRoomObject.send({ type: "REMOVE_PLAYER" });
    initiateRoomUpdate();
    loader(true);
    bingoRoomObject.leave();
}

function loader(value = true, text = '') {
    store.state.loading = value;
    store.state.loadingText = text;
    if (!value) store.state.loadingText = '';
}

function isLoading() {
    return store.state.loading;
}

function removeRoom() {
    store.state.room.myRoom =
        store.state.room.myAdminPlayer =
        store.state.room.myRoomCurrentPlayer =
        store.state.room.gameStarted =
        store.state.room.myRoomAdmin = null;
    router.replace({ path: '/' });
}

export default {
    initMainRoom,
    createBingoRoom,
    addStateChangeLitsener,
    joinBingoRoom,
    playMyTurn,
    startGame,
    reJoin,
    leaveRoom,
    initiateRoomUpdate,
    reopenRoom,
    popEmoji
};
