import Vue from "vue";
import Vuex from "vuex";
import VueRouter from 'vue-router'
import App from "./App.vue";
import BootstrapVue from "bootstrap-vue";
import store from "./store";
import router from "./router";
import Helpers from "./helpers";
import server from "./server";
import "bootstrap/dist/css/bootstrap.css";
import "bootstrap-vue/dist/bootstrap-vue.css";
import Unicon from 'vue-unicons'
import {
  uniEditAlt, uniCog
} from 'vue-unicons/src/icons'
Unicon.add([uniEditAlt, uniCog]);
Vue.use(Unicon);

// make store available in browser console for development purpose
if (window.webpackHotUpdate) {
  window.STORE = store;
}
// in production show some funny message ഹീ ഹീ ഹീ!!! and nicely handle the errors
else {
  Helpers.browser.hackWarn();
  Vue.config.errorHandler = (error) => {
    console.error(error);
    server.leaveRoom();
  }
}

Vue.use(Vuex);
Vue.use(BootstrapVue);
Vue.use(VueRouter);

Vue.config.productionTip = false;

window.addEventListener("resize", Helpers.browser.calculateSize);
server.initMainRoom();

new Vue({
  router,
  render: h => h(App)
}).$mount("#app");
